import React, { useCallback, useEffect, useState } from 'react'
import PropTypes from 'prop-types'

import { isControlBinding } from './util'
import styles from './styles.module.css'
import { DropZoneContainer } from './DragAndDrop'
import SequenceModal from './SequenceModal'

import Key from '../Keys/Key'
import KeyWrapper from '../Keys/KeyWrapper'

import ActionButton from '../../Common/ActionButton'
import Icon from '../../Common/Icon'
import { useModalState } from '../../Common/Modal'

import { stop } from '../../event-util'
import { replaceIndex, repositionElement, withoutIndex } from '../../reducer-util'

export default function MacroBinds (props) {
  const { bindings, onUpdate } = props
  const [dragging, setDragging] = useState(null)

  const [
    isSequenceModalOpen,
    openSequenceModal,
    closeSequenceModal
  ] = useModalState()

  const [
    handleUpdateBinding,
    handleDeleteBinding,
    handleAddBindings,
    handleAddControlBinding,
    handleAddKeyBinding
  ] = useBindingUpdaters(bindings, onUpdate)

  function handleDragStart (event, index) {
    if (event.dataTransfer.items.length) {
      event.dataTransfer.clearData()
      return
    }

    const binding = bindings[index]
    setDragging({
      index,
      mode: isControlBinding(binding) ? 'row' : 'col',
      isControlBinding: isControlBinding(binding)
    })
  }

  useEffect(() => {
    function clearDrop () {
      setDragging(null)
    }
    function clearDrag (event) {
      event.stopPropagation()
      event.preventDefault()
    }

    document.body.addEventListener('drop', clearDrop)
    document.body.addEventListener('dragover', clearDrag)
    return () => {
      document.body.removeEventListener('drop', clearDrop)
      document.body.removeEventListener('dragover', clearDrag)
    }
  }, [setDragging])

  function handleDrop (targetIndex) {
    onUpdate(repositionElement(bindings, dragging.index, targetIndex))
    setDragging(null)
  }

  return (
    <>
      <div className={styles.macroBindings}>
        {bindings.map((bind, i) => (
          <React.Fragment key={i}>
            <KeyWrapper
              key={i}
              data-index={i}
              themeClass={isControlBinding(bind) ? styles.controlBinding : ''}
              draggable
              onDragStart={e => handleDragStart(e, i)}
            >
              <Key
                context={isControlBinding(bind) ? 'macros' : undefined}
                value={bind.value}
                params={bind.params}
                onUpdate={updated => handleUpdateBinding(i, updated)}
              />
              <div className={styles.actions}>
                <Icon
                  name="trash"
                  className={styles.deleteBind}
                  onClick={stop(() => handleDeleteBinding(i))}
                />
              </div>
            </KeyWrapper>
          </React.Fragment>
        ))}
        <DropZoneContainer
          dragging={dragging}
          bindings={bindings}
          onDrop={handleDrop}
        />
      </div>

      {isSequenceModalOpen && (
        <SequenceModal onClose={bindings => {
          closeSequenceModal()
          handleAddBindings(bindings)
        }} />
      )}

      <div className={styles.macroActions}>
        <ActionButton onClick={handleAddControlBinding}>
          Add Control Binding
        </ActionButton>
        <ActionButton onClick={handleAddKeyBinding}>
          Add Key Binding
        </ActionButton>
        <ActionButton onClick={openSequenceModal}>
          Add String Sequence
        </ActionButton>
      </div>
    </>
  )
}

MacroBinds.propTypes = {
  bindings: PropTypes.array.isRequired,
  macroIndex: PropTypes.number.isRequired,
  dispatch: PropTypes.func.isRequired
}

function useBindingUpdaters (bindings, onUpdate) {
  const handleUpdateBinding = useCallback((index, binding) => {
    onUpdate(replaceIndex(bindings, index, binding))
  }, [bindings, onUpdate])

  const handleDeleteBinding = useCallback(index => {
    onUpdate(withoutIndex(bindings, index))
  }, [bindings, onUpdate])

  const handleAddBindings = useCallback(newBindings => {
    onUpdate([...bindings, ...newBindings])
  }, [bindings, onUpdate])

  const handleAddControlBinding = useCallback(() => {
    handleAddBindings([{ value: '&macro_press', params: [] }])
  }, [handleAddBindings])

  const handleAddKeyBinding = useCallback(() => {
    handleAddBindings([{ value: '&kp', params: [] }])
  }, [handleAddBindings])

  return [
    handleUpdateBinding,
    handleDeleteBinding,
    handleAddBindings,
    handleAddControlBinding,
    handleAddKeyBinding
  ]
}
