import isEqual from 'lodash/isEqual'
import { Fragment, useMemo, useState } from 'react'

import { isControlBinding } from './util'
import styles from './drag-and-drop.module.css'

export function groupIntoRows (bindings) {
  return bindings.reduce((rows, bind, i) => {
    const addRow = () => rows.push([])
    const addToRow = () => rows.at(-1).push({ ...bind, index: i })
    const isRowEmpty = () => rows.at(-1).length === 0
    const isRowFull = () => (
      (!isRowEmpty() && isControlBinding(rows.at(-1)[0])) ||
      rows.at(-1).length === 6
    )

    if (isControlBinding(bind) && !isRowEmpty()) {
      addRow()
    } else if (isRowFull()) {
      addRow()
    }

    addToRow()

    return rows
  }, [[]])
}

export function DropZoneContainer ({ bindings, children, dragging, dragMode, ...rest }) {
  const [currentTarget, setCurrentTarget] = useState([null, null])
  const bindingRows = useMemo(
    () => groupIntoRows(bindings),
    [bindings]
  )

  function handleDragEnter (target) {
    console.log('enter', target)
    setCurrentTarget(() => target)
  }

  function isCurrentTarget (target) {
    return isEqual(currentTarget, target)
  }

  function handleDragLeave (target) {
    setCurrentTarget(state => {
      console.log('leaving', target, 'currently in', state)
      return isEqual(state, target) ? [null, null] : state
    })
  }

  function handleDrop () {
    console.log('handleDrop', currentTarget)
    const [row, col, half] = currentTarget
    const { index } = bindingRows[row][col]
    const next = index < bindings.length - 1
      ? index + 1
      : -1

    rest.onDrop(half === 1 ? next : index)
  }

  return dragging && (
    <div className={[
      styles.dropTargets,
      dragging ? styles.dragging : '',
      dragMode === 'row' ? styles.dropRowOnly : ''
    ].join(' ')}>
      {bindingRows.map((row, i) => (
        <Fragment key={i}>
          <DropZoneRow
            row={row}
            isDropping={isCurrentTarget([i, 0, -1])}
            onDragEnter={() => handleDragEnter([i, 0, -1])}
            onDragLeave={() => handleDragLeave([i, 0, -1])}
            onDrop={handleDrop}
          />
          {row.map((bind, j) => (
            <Fragment key={j}>
              <DropZoneBefore
                bind={bind}
                isDropping={isCurrentTarget([i, j, 0])}
                onDragEnter={() => handleDragEnter([i, j, 0])}
                onDragLeave={() => handleDragLeave([i, j, 0])}
                onDrop={handleDrop}
              />
              <DropZoneAfter
                bind={bind}
                isDropping={isCurrentTarget([i, j, 1])}
                onDragEnter={() => handleDragEnter([i, j, 1])}
                onDragLeave={() => handleDragLeave([i, j, 1])}
                onDrop={handleDrop}
                data-extend={
                  !isControlBinding(bind) && j === row.length - 1
                    ? Math.max(0, 5 - j)
                    : 0
                }
              />
            </Fragment>
          ))}
        </Fragment>
      ))}
      <DropZoneRow
        row={bindingRows.length - 1}
        isDropping={isCurrentTarget([bindingRows.length - 1, bindingRows.at(-1).length - 1, 1])}
        onDragEnter={() => handleDragEnter([bindingRows.length - 1, bindingRows.at(-1).length - 1, 1])}
        onDragLeave={() => handleDragLeave([bindingRows.length - 1, bindingRows.at(-1).length - 1, 1])}
        onDrop={handleDrop}
      />
    </div>
  )
}

function DropZone ({ isDropping, control = false, classes = [], ...rest }) {
  return (
    <div
      data-is-control={control}
      className={[
        styles.dropTargetHalf,
        isDropping ? styles.droppable : '',
        ...classes
      ].join(' ')}
      {...rest}
    />
  )
}

export function DropZoneBefore ({ bind, ...rest }) {
  return (
    <DropZone control={isControlBinding(bind)} {...rest} />
  )
}

export function DropZoneAfter ({ bind, ...rest }) {
  return (
    <DropZone control={isControlBinding(bind)} classes={[styles.secondHalf]} {...rest} />
  )
}

export function DropZoneRow ({ row, isDropping, ...rest }) {
  return (
    <div
      data-drop-mode="row"
      className={[
        styles.dropTargetRow,
        isDropping ? styles.droppable : ''
      ].join(' ')}
      {...rest}
    />
  )
}
