import { useCallback, useState } from 'react'
import PropTypes from 'prop-types'

import Input from '../../../Common/Input'
import { devicetreeNodeShape } from './nodePropTypes'
import {
  validateDevicetreeLabel,
  validateDevicetreeNodeName
} from './validators'

// TODO: break this apart to match individual toggles
function isCombined (node) {
  const { name } = node
  const { labels: [nodeLabel] = [] } = node
  const { label: labelProp } = node.properties

  return (
    name.toLowerCase() === (nodeLabel || '').toLowerCase() &&
    name.toLowerCase() === (labelProp?.parsed || '').toLowerCase()
  )
}

export default function NodeIdentifiers ({ node, onUpdate }) {
  // TODO: trigger value updates when toggling
  const [nameFromBind, setNameFromBind] = useState(isCombined(node))
  const [labelFromBind, setLabelFromBind] = useState(isCombined(node))
  const [
    handleUpdateNodeName,
    handleUpdateNodeLabel,
    handleUpdateLabelProperty
  ] = useIdentifierUpdaters(node, onUpdate, nameFromBind, labelFromBind)

  return (
    <table>
      <tbody>
        <tr>
          <th>Bind</th>
          <td>
            <Input
              type="text"
              value={node.labels[0] || ''}
              required={true}
              validator={validateDevicetreeLabel}
              onChange={handleUpdateNodeLabel}
            />
          </td>
        </tr>
        <tr>
          <th>Node</th>
          <td>
            <Input
              value={node.name}
              required={true}
              readOnly={nameFromBind}
              disabled={nameFromBind}
              validator={validateDevicetreeNodeName}
              onChange={handleUpdateNodeName}
            />
            <label title="Set automatically using bind name">
              <input
                type="checkbox"
                checked={nameFromBind}
                onChange={e => setNameFromBind(e.target.checked)}
              /> Automatic
            </label>
          </td>
        </tr>
        <tr>
          <th>Label</th>
          <td>
            <Input
              type="text"
              value={node.properties.label.parsed || ''}
              required={true}
              readOnly={labelFromBind}
              disabled={labelFromBind}
              validator={() => null}
              onChange={handleUpdateLabelProperty}
            />
            <label title="Set automatically using bind name">
              <input
                type="checkbox"
                checked={labelFromBind}
                onChange={e => setLabelFromBind(e.target.checked)}
              /> Automatic
            </label>
          </td>
        </tr>
      </tbody>
    </table>
  )
}

function useIdentifierUpdaters (node, onUpdate, nameFromBind, labelFromBind) {
  const handleUpdateNodeName = useCallback(e => {
    onUpdate({ ...node, name: e.target.value })
  }, [node, onUpdate])

  const handleUpdateNodeLabel = useCallback(e => {
    const { value } = e.target
    const labels = [value, ...node.labels.slice(1)]
    const name = nameFromBind ? value : node.name
    const properties = labelFromBind
      ? {
          ...node.properties,
          label: {
            parsed: value.toUpperCase(),
            raw: `"${value.toUpperCase()}"`,
            useRaw: false
          }
        }
      : node.properties

    onUpdate({ ...node, name, labels, properties })
  }, [node, onUpdate, nameFromBind, labelFromBind])

  const handleUpdateLabelProperty = useCallback(e => onUpdate({
    ...node,
    properties: {
      ...node.properties,
      label: {
        ...node.properties.label,
        parsed: e.target.value,
        useRaw: false
      }
    }
  }), [node, onUpdate])

  return [
    handleUpdateNodeName,
    handleUpdateNodeLabel,
    handleUpdateLabelProperty
  ]
}

NodeIdentifiers.propTypes = {
  node: PropTypes.shape(devicetreeNodeShape).isRequired,
  onUpdate: PropTypes.func.isRequired
}
