import PropTypes from 'prop-types'

export const propValueShape = {
  useRaw: PropTypes.bool,
  raw: PropTypes.string,
  parsed: PropTypes.any
}

export const devicetreeNodeShape = {
  name: PropTypes.string.isRequired,
  labels: PropTypes.arrayOf(PropTypes.string).isRequired,
  properties: PropTypes.objectOf(function (propValue, key, componentName, location, propFullName) {
    try {
      PropTypes.checkPropTypes({
        propValue: PropTypes.shape(propValueShape).isRequired
      }, { propValue }, location, componentName)
    } catch (err) {
      return new Error(
        'Invalid prop `' + propFullName + '` supplied to' +
        ' `' + componentName + '`. Validation failed.'
      )
    }
  }).isRequired
}
