import PropTypes from 'prop-types'
import { useCallback, useState } from 'react'

import MacroBinds from './MacroBinds'
import ConfigModal from './ConfigModal'
import styles from './styles.module.css'
import Icon from '../../Common/Icon'
import { customizeMacroOptions, updateMacroBindings, deleteMacro } from './macrosSlice'

function Macro (props) {
  const { index, macro, dispatch } = props
  const [showConfig, setShowConfig] = useState(false)

  const openConfigModal = useCallback(function (event) {
    event.stopPropagation && event.stopPropagation()
    setShowConfig(true)
  }, [setShowConfig])

  function handleUpdateConfig (options) {
    setShowConfig(false)
    dispatch(customizeMacroOptions({ macroIndex: index, options }))
  }

  const handleUpdateBindings = useCallback(bindings => {
    dispatch(updateMacroBindings({ index, bindings }))
  }, [dispatch, index])

  return (
    <div className={styles.macro}>
      {showConfig && (
        <ConfigModal macro={macro} onUpdate={handleUpdateConfig} />
      )}
      <div className={styles.heading}>
        <h5 onClick={openConfigModal}>
          &amp;{macro.labels[0]}
        </h5>
        <div>
          <Icon
            name="wrench"
            className={`${styles.action}`}
            onClick={openConfigModal}
          />
          <Icon
            name="trash-can"
            className={`${styles.action} ${styles.remove}`}
            onClick={() => dispatch(deleteMacro({ index }))}
          />
        </div>
      </div>
      <MacroBinds
        bindings={macro.properties.bindings.parsed}
        onUpdate={handleUpdateBindings}
      />
    </div>
  )
}

Macro.propTypes = {
  index: PropTypes.number.isRequired,
  macro: PropTypes.shape({
    name: PropTypes.string.isRequired,
    labels: PropTypes.arrayOf(PropTypes.string).isRequired,
    properties: PropTypes.object.isRequired
  }).isRequired,
  dispatch: PropTypes.func.isRequired
}

export default Macro
