import { Fragment, useCallback, useContext, useMemo, useState } from 'react'

import Modal from '../../Common/Modal'
import DialogBox from '../../Common/DialogBox'
import KeyWrapper from '../Keys/KeyWrapper'
import styles from './styles.module.css'
import { hydrateTree } from '../Binding/util'
import { SearchContext } from '../../providers'
import KeycodeBindNode from '../../EnhancedPickers/Keycode/KeycodeBindNode'
import ActionButton from '../../Common/ActionButton'

const singleValue = value => ({ value: '&kp', params: [{ value, params: [] }] })

const patternMappings = [
  { pattern: /[0-9]/, convert: digit => singleValue(`N${digit}`) },
  { pattern: /[a-z]/, convert: char => singleValue(char.toUpperCase()) },
  {
    pattern: /[A-Z]/,
    convert: char => ({
      value: '&kp',
      params: [{
        value: 'LS',
        params: [{
          value: char.toUpperCase(),
          params: []
        }]
      }]
    })
  }
]

const simpleMappings = {
  '!': singleValue('EXCL'),
  '@': singleValue('AT'),
  '#': singleValue('POUND'),
  $: singleValue('DLLR'),
  '%': singleValue('PRCNT'),
  '^': singleValue('CARET'),
  '&': singleValue('AMPS'),
  '*': singleValue('STAR'),
  '(': singleValue('LPAR'),
  ')': singleValue('RPAR'),
  '\n': singleValue('RET'),
  ' ': singleValue('SPACE'),
  '-': singleValue('MINUS'),
  _: singleValue('UNDER'),
  '=': singleValue('EQUAL'),
  '+': singleValue('PLUS'),
  '[': singleValue('LBKT'),
  ']': singleValue('LBRC'),
  '{': singleValue('RBKT'),
  '}': singleValue('RBRC'),
  '\\': singleValue('BSLH'),
  '|': singleValue('PIPE'),
  '': singleValue('TILDE2'),
  ';': singleValue('SEMI'),
  ':': singleValue('COLON'),
  '\'': singleValue('SQT'),
  '"': singleValue('DQT'),
  '`': singleValue('GRAVE'),
  '~': singleValue('TILDE'),
  ',': singleValue('COMMA'),
  '<': singleValue('LT'),
  '.': singleValue('DOT'),
  '>': singleValue('GT'),
  '/': singleValue('FSLH'),
  '?': singleValue('QMARK')
}

function mapCharacter (char) {
  if (simpleMappings[char]) {
    return simpleMappings[char]
  }

  for (const { pattern, convert } of patternMappings) {
    if (char.match(pattern)) {
      return convert(char)
    }
  }
}

function SequenceModal ({ onClose }) {
  const { sources, mergedGlyphs } = useContext(SearchContext)
  const [string, setString] = useState('Type something...')
  const sequence = useMemo(() => {
    return string.split('').reduce((sequence, char) => {
      const mapped = mapCharacter(char)
      if (mapped) {
        sequence.push(mapped)
      }

      return sequence
    }, [])
  }, [string])

  const bindings = sequence.map(({ value, params }) => hydrateTree(
    value,
    params,
    sources,
    undefined,
    mergedGlyphs
  ))

  const focusInput = useCallback(node => {
    if (node) {
      node.focus()
      node.select()
    }
  }, [])

  return (
    <Modal>
      <DialogBox dismissText={false}>
        <h2>Create Typing Sequence</h2>
        <p>Type a message to generate a sequence of bindings in the macro.</p>

        <textarea
          value={string}
          ref={focusInput}
          style={{ width: '100%', height: '6rem' }}
          onChange={e => setString(e.target.value)}
        />

        <div className={styles.macroBindings}>
          {bindings.map((bind, i) => (
            <Fragment key={i}>
              <KeyWrapper selectable={false}>
                <KeycodeBindNode
                  node={bind.params[0]}
                  onSelect={() => {}}
                />
              </KeyWrapper>
            </Fragment>
          ))}
        </div>

        <div className={styles.macroActions}>
          <ActionButton onClick={() => onClose(bindings)}>Add to macro</ActionButton>
          <ActionButton onClick={() => onClose([])} secondary>Cancel</ActionButton>
        </div>
      </DialogBox>
    </Modal>
  )
}

export default SequenceModal
