import PropTypes from 'prop-types'
import { useCallback, useState } from 'react'

import ActionButton from '../../Common/ActionButton'
import DialogBox from '../../Common/DialogBox'
import Modal from '../../Common/Modal'
import { validateDevicetreeLabel } from '../Common/Devicetree/validators'
import Input from '../../Common/Input'

export default function NewMacroModal ({ onCreate, onCancel }) {
  const [name, setName] = useState('new_macro')
  const handleNameChange = useCallback(event => setName(event.target.value), [setName])

  const handleCreate = useCallback(() => {
    onCreate(macroFromName(name))
  }, [name, onCreate])

  const error = validateDevicetreeLabel(name)

  return (
    <Modal onDismiss={onCancel}>
      <DialogBox dismissText={null}>
        <h3>Add new macro</h3>
        <div>
          <label>Macro name: <Input
            required
            value={name}
            validator={validateDevicetreeLabel}
            onChange={handleNameChange}
          /></label>
        </div>
        <p>
          <em>
            This value will be used for binding the macro's actions in your
            keymap (e.g. <code>&amp;{name}</code>) and should be unique. It will
            also be used internally for the node name and label property, which
            you can customize later.
          </em>
        </p>
        <div style={{ textAlign: 'center', margin: '10px' }}>
          <ActionButton
            disabled={!name || error}
            onClick={handleCreate}
          >
            Create
          </ActionButton>
          <ActionButton onClick={onCancel} secondary>Cancel</ActionButton>
        </div>
      </DialogBox>
    </Modal>
  )
}

NewMacroModal.propTypes = {
  onCreate: PropTypes.func.isRequired,
  onCancel: PropTypes.func.isRequired
}

function macroFromName (name) {
  const labels = [name]
  const label = name.toUpperCase()

  return {
    name,
    labels,
    properties: {
      compatible: {
        parsed: 'zmk,behavior-macro',
        raw: '"zmk,behavior-macro"'
      },
      label: {
        parsed: label,
        raw: `"${label}"`
      },
      '#binding-cells': {
        parsed: 0,
        raw: '<0>'
      },
      bindings: {
        parsed: [{ value: '&none', params: [] }],
        raw: '&none'
      }
    }
  }
}
