import { useState } from 'react'

import Modal from '../../Common/Modal'
import DialogBox from '../../Common/DialogBox'
import styles from './styles.module.css'
import NodeIdentifiers from '../Common/Devicetree/NodeIdentifiers'

function ConfigModal ({ macro, onUpdate }) {
  const [pending, setPending] = useState(macro)

  function handleConfirm () {
    onUpdate(pending)
  }

  function handleCancel () {
    onUpdate(macro)
  }

  return (
    <Modal onDismiss={handleCancel}>
      <DialogBox onDismiss={handleConfirm}>
        <h2>Customize <code>{macro.name}</code></h2>
        <div>
          <NodeIdentifiers
            node={pending}
            onUpdate={setPending}
          />

          <div className={styles.field}>
            <label htmlFor="customize-macro-wait">
              Wait (ms)
            </label>
            <input
              id="customize-macro-wait"
              type="number"
              placeholder="Default"
              value={pending.properties['wait-ms']?.parsed || ''}
              onChange={event => (
                setPending(state => ({
                  ...state,
                  properties: {
                    ...state.properties,
                    'wait-ms': event.target.value === ''
                      ? undefined
                      : { parsed: Number(event.target.value) }
                  }
                }))
              )}
            />
          </div>

          <div className={styles.field}>
            <label htmlFor="customize-macro-tap">
              Tap (ms)
            </label>
            <input
              id="customize-macro-tap"
              type="number"
              placeholder="Default"
              value={pending.properties['tap-ms']?.parsed || ''}
              onChange={event => (
                setPending(state => ({
                  ...state,
                  properties: {
                    ...state.properties,
                    'tap-ms': event.target.value === ''
                      ? undefined
                      : { parsed: Number(event.target.value) }
                  }
                }))
              )}
            />
          </div>

        </div>
      </DialogBox>
    </Modal>
  )
}

export default ConfigModal
