
export const withoutIndex = (array, index) => [
  ...array.slice(0, index),
  ...array.slice(index + 1)
]

export const replaceIndex = (array, index, value) => [
  ...array.slice(0, index),
  value,
  ...array.slice(index + 1)
]

export function repositionElement (array, index, target) {
  if (target === -1) {
    return [...array.filter((_, i) => i !== index), array[index]]
  }

  const reordered = array.reduce((arr, val, i) => {
    if (i === target) arr.push(array[index])
    if (i !== index) arr.push(val)
    return arr
  }, [])

  return reordered
}
