import { useCallback } from 'react'
import { useSelector, useDispatch } from 'react-redux'

import Macro from './Macro'
import styles from './styles.module.css'

import { useModalState } from '../../Common/Modal'
import ActionButton from '../../Common/ActionButton'
import Link from '../../Common/Link'
import Icon from '../../Common/Icon'

import { selectMacros, addMacro } from './macrosSlice'
import NewMacroModal from './NewMacroModal'

function MacroEditor () {
  const macros = useSelector(selectMacros)
  const dispatch = useDispatch()

  const [showNewMacro, openNewMacro, closeNewMacro] = useModalState()
  const handleNewMacro = useCallback(macro => {
    dispatch(addMacro(macro))
    closeNewMacro()
  }, [closeNewMacro, dispatch])

  return (
    <div className={styles.main}>
      <div className={styles.sidebar}>
        <Link external href="https://zmk.dev/docs/behaviors/macros">
          <Icon name="book" /> ZMK Docs: Macros
        </Link>

        <ActionButton onClick={openNewMacro}>
          Add New Macro
        </ActionButton>

        {showNewMacro && (
          <NewMacroModal
            onCreate={handleNewMacro}
            onCancel={closeNewMacro}
          />
        )}
      </div>

      {macros.map((macro, i) => (
        <Macro
          key={i}
          macro={macro}
          index={i}
          dispatch={dispatch}
        />
      ))}
    </div>
  )
}

export default MacroEditor
